import {Directive, Input} from '@angular/core';
import {AdaptChartAbstract, AdaptChartAxis, AdaptChartSeries, AdaptChartSeriesFormat} from './chart.model';

export enum AdaptStackedChartType {
  Bar = 'bar',
  Column = 'column',
  Area = 'area'
}

export type AdaptStackedChartSeries = AdaptChartSeries<number>;

@Directive()
export abstract class AdaptStackedChartAbstract<T> extends AdaptChartAbstract {
  @Input() type: AdaptStackedChartType;
  @Input() xAxis?: AdaptChartAxis;
  @Input() yAxis?: AdaptChartAxis;
  @Input() abstract series: AdaptChartSeries<T>[];
  @Input() hideAxises?: boolean;
  @Input() seriesDisplay: AdaptChartSeriesFormat;
  @Input() showSlider?: boolean;
  @Input() slidePointsCount?: number;                   // Sets the count of data portions per slide
}
